img{
    height: 500px;
    width: 960px;

}

.bienvenidos{
    width: 70%;
    padding-right: 20px;
    box-sizing: border-box;
    margin-right: 10px;
}
.testimonios{
    width: 30%;

}
.testimonio .cita{
    background-color: rgb(145, 160, 117);
    font-style: italic;
    display: block;
    font-size: 22px;
}
.testimonio .autor{
    font-size: smaller;
    font-weight: bold;
    text-align: right;
    display: block;
    margin: 12px 0;

}
.columnas{
    display: flex;
    justify-content: space-between;
    gap: 20px;
}
@media (max-width: 600px) {
    body{
    font-size: 12px;
    line-height: 15px;
    }
    img{
        height: 200px;
        width: 400px;
    
    }
  }
  @media (max-width: 900px) {
    body{
      font-size: 14px;
      line-height: 18px;
      }
      img{
        height: 400px;
        width: 900px;
    
    }
    }
  